<template>
  <div class="main">
    <sidebar/>
    <div class="main__container">
      <div class="main__tasks">
        <div v-for="(item, index) in arr" :key="index" class="main__task">
          <button @click="exitBlock(index)=== index" class="main__exits">X</button>

          <h3>{{ item.title }}</h3>
          <p>{{ item.deadline }}</p>
          <p v-for="item in item.tags">
            {{ item }}
          </p>
          <p>{{ new Date(item.date).toLocaleString() }}</p>
        </div>
      </div>
      <div :style="{'marginRight':`${margin}%`}"  class="main__form">
        <h3>Добавить новую задачу</h3>
        <p v-if="visible" class="alert">Введите обязательное поле</p>
        <input @focus="inpFocus" placeholder="Введите задачу" class="main__title" v-model="tasks.title" type="text">
        <input type="date" v-model="tasks.deadline">
        <div class="main__tag">
          <input placeholder="Введите подзадачу" v-model="tag" type="text">
          <button @click="addTag">+</button>
        </div>
        <div class="main__tags">
          <div v-for="(item,index) in tasks.tags" :key="index" class="main__item">
            <p>{{ item }}</p>
            <button @click="deleteTag(index)===index">-</button>
          </div>

        </div>
        <button @click="funcValidate" class="main__addTask">Добавить</button>
      </div>
    </div>
    <button @click="addMargin" class="openForm">+</button>
    <div  @mouseover="mrgMove" class="mouseZone">
      <div class="arrow">
        <img src="@/assets/icons/arrows.png" alt="">
      </div>
    </div>


  </div>
</template>

<script>
import Sidebar from "@/components/sidebar"

export default {
  components: {
    Sidebar
  },
  data() {
    return {
      tag: '',
      visible:false,
      margin:null,
      tasks: {
        title: '',
        date: new Date(),
        deadline: '',
        tags: []
      },
      arr: [],
    }
  },
  methods: {
    mrgMove () {
        this.margin = 0;
    },
    addMargin () {
      if (this.margin < 25) {
        this.margin = 0;
      }else  {
        this.margin = null;
      }
    },
    funcValidate () {
      if (this.tasks.title.length !== 0) {
        this.addTask();
        this.margin = -25;
      }else {
        this.visible = true
      }

    },
    addTask() {
     //this.arr.push(this.tasks)
      this.arr.push({
        title: this.tasks.title,
        date: new Date(),
        deadline: this.tasks.deadline,
        tags: this.tasks.tags
      });
      this.tasks.title = "";
      this.tasks.deadline = "";
      localStorage.setItem('tasks', JSON.stringify(this.arr));
    },
    addTag() {
      if ( this.tag.length !== 0) {
        this.tasks.tags.push(this.tag);
      }

    },
    deleteTag(index) {
      this.tasks.tags.splice(index, 1)
      // this.tags.splice(index, 1);
    },
    tagsUpdate() {
      console.log(localStorage.getItem('tasks'))
      if (localStorage.getItem('tasks')) {
        this.arr = JSON.parse(localStorage.getItem("tasks"))

        console.log(this.arr);
      }
    },
    exitBlock (index) {
      this.arr.splice(index,1);
      console.log(this.arr);
      localStorage.setItem("tasks",JSON.stringify(this.arr))
    },
    inpFocus() {
      this.visible = false
    }


  },
  computed: {
  },
  mounted() {
    this.tagsUpdate();

  },

}
</script>

